<template>
  <div class="view">
    <div class="view-container">
      <c-header></c-header>
      <c-table :dishes=dishes></c-table>
      <!-- <c-pagination></c-pagination> -->
    </div>
  </div>
</template>

<script>
import CHeader from './components/Header'
// import CPagination from './components/Pagination'
import CTable from './components/Table'
import axios from 'axios'

export default {
  name: 'TodoList',
  components: {
    CHeader,
    // CPagination,
    CTable
  },
  data () {
    return {
      dishes: []
    }
  },
  methods: {
    getWaiteringOrderList () {
      axios.get('/api/service/scr/app/waiter/getWaiteringOrderList')
        .then(this.getWaiteringOrderListSucc)
    },
    getWaiteringOrderListSucc (res) {
      const data = res.data.data
      if (res.status === 200 && data) {
        console.log(data)
        this.dishes = data
      }
    }
  },
  mounted () {
    this.getWaiteringOrderList()
  }
}
</script>

<style lang="stylus" scoped>
@import '~styles/viewpadding.styl'
  .view
    min-width 5.6rem
    pad1()
    .view-container
      pad2()
      min-height 50vw
      position relative
      box-sizing border-box
      // min-width 11.2rem
</style>



