<template>
  <div class="header">
    <div class="title"><span @click="open_add" >添加</span></div>
    <div class="header-right">
      <div class="search">
        <div><input type="text" class="search-input" placeholder="用户名"></div>
        <div class="search-btn">&#xe632;</div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'CHeader',
  methods: {
    open_add () {
      this.$emit('openAdd', true)
    }
  }
}
</script>

<style lang="stylus" scoped>
@import '~styles/viewpadding.styl'

  .header
    space-between()
    box-sizing border-box
    .title
      mb028()
      span
        list-button()
    .header-right
      space-between()
      box-sizing border-box
      min-width 6rem
      .status
        mb028()
        width 3rem
        border 1px solid #aaf
        margin-right .28rem

      .search
        space-between()
        min-width 4rem
        box-sizing border-box
        .search-input
          mb028()
          background #eee
          height .56rem
          line-height .56rem
          min-width 3.66rem
          margin-right .14rem
          padding 0 .1rem
          border-radius .1rem
        .search-btn
          mb028()
          min-width .7rem
          height .56rem
          line-height .56rem
          font-family 'iconfont'
          font-size .40rem
          color #aaf
</style>



