import Vue from 'vue'
import Router from 'vue-router'
import TodoList from '@/pages/todo/TodoList'
import TodaydoList from '@/pages/todaydo/TodaydoList'
import PersonManage from '@/pages/manage/PersonManage'
import Statistics from '@/pages/statistics/Statistics'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      redirect: '/todoList'
    },
    {
      path: '/todoList',
      name: 'TodoList',
      component: TodoList
    },
    {
      path: '/todaydoList',
      name: 'TodaydoList',
      component: TodaydoList
    },
    {
      path: '/personManage',
      name: 'PersonManage',
      component: PersonManage
    },
    {
      path: '/statistics',
      name: 'Statistics',
      component: Statistics
    }
  ]
})



