<template>
  <div class="table-wrapper">
    <table class="table">
      <thead>
        <tr class="title">
          <th>序号</th>
          <th>商品名称</th>
          <th>商品编号</th>
          <th>完成时间</th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="(item, index) of list" :key="index">
          <td>{{index + 1}}</td>
          <td>{{item.productName}}</td>
          <td>{{item.orderNo ? item.orderNo : 'KA0001'}}</td>
          <td>{{item.endTime ? item.endTime : '2018-03-11 18:35:25'}}</td>
        </tr>
        <tr>
          <td>1</td>
          <td>红烧排骨</td>
          <td>KA00002</td>
          <td>2018-03-11 19:00:46</td>
        </tr>
      </tbody>
    </table>
  </div>
</template>

<script>

export default {
  name: 'CTable',
  props: {
    list: Array
  }
}
</script>

<style lang="stylus" scoped>
@import '~styles/viewpadding.styl'
  .table-wrapper
    // background #ee8
    .table
      width 100%
      margin-bottom 1.6rem
      // border 1px solid #000
      .title
        height .62rem
        line-height .62rem
        background #f2f2f2
        border 1px solid #f2f2f2
      th
        text-align center
      td
        text-align center
        padding .2rem
        border 1px solid #e4e4e4
</style>



