<template>
  <div class="pagination">
    <span class="prev">上一页</span>
    <span class="p1 p-active">1</span>
    <span class="p2">2</span>
    <span class="p3">3</span>
    <span class="p4">4</span>
    <span class="next">下一页</span>
  </div>
</template>

<script>
export default {
  name: 'CPagination'
}
</script>

<style lang="stylus" scoped>
@import '~styles/viewpadding.styl'

  .pagination
    // background #eee
    text-align center
    margin-bottom 1.4rem
    span
      display inline-block
      font-size .28rem
      border 1px solid #e4e4e4
      margin-right .14rem
      padding .07rem .21rem
    .prev
      // background #ff0
      padding .14rem .42rem
      margin-right .28rem
    .next
      padding .14rem .42rem
    .p-active
      background #3a7ab1
      color #fff
</style>



