<template>
  <div class="view">
    <div class="view-container">
      <c-header></c-header>
      <c-table :list="orderList"></c-table>
      <c-pagination
        @pageChange="handlePageChange"
        :totalPage="totalPage"/>
    </div>
  </div>
</template>

<script>
import CHeader from './components/Header'
import CTable from './components/Table'
import CPagination from './components/Pagination'
import axios from 'axios'

export default {
  name: 'TodaydoList',
  components: {
    CHeader,
    CTable,
    CPagination
  },
  data () {
    return {
      pageNum: 1,
      totalPage: 0, // 总页数
      orderList: []
    }
  },
  watch: {
    pageNum () {
      this.getWaiteredOrderList(this.pageNum)
    }
  },
  methods: {
    getWaiteredOrderList (pageNum) {
      axios.post('/api/service/scr/app/waiter/getWaiteredOrderList', {
        pageNum: pageNum
      })
        .then(this.getWaiteredOrderListSucc)
    },
    getWaiteredOrderListSucc (res) {
      console.log(res)
      const data = res.data.data
      if (res.status === 200 && data) {
        // console.log(data)
        // console.log(res.data)
        this.totalPage = res.data.pagerCount
        this.orderList = data
      }
    },
    handlePageChange (num) {
      // console.log(num)
      this.getWaiteredOrderList(num)
    }
  },
  mounted () {
    this.getWaiteredOrderList()
  }
}
</script>

<style lang="stylus" scoped>
@import '~styles/viewpadding.styl'
  .view
    min-width 5.6rem
    pad1()
    .view-container
      pad2()
      min-height 50vw
      position relative
      box-sizing border-box
      // min-width 11.2rem
</style>



