    }
  }
}
</script>

<style lang="stylus" scoped>
@import '~styles/viewpadding.styl'
  .table-wrapper
    // background #ee8
    .table
      width 100%
      margin-bottom 1.6rem
      // border 1px solid #000
      .title
        height .62rem
        line-height .62rem
        background #f2f2f2
        border 1px solid #f2f2f2
      th
        text-align center
      td
        text-align center
        padding .2rem
        border 1px solid #e4e4e4
        img
          margin-right .2rem
        .change-btn
          list-button()
          background #38A8FE
          cursor pointer
</style>



