<template>
  <div class="mask">
    <div class="popup">
      <div class="header">
        <span class="title">用户信息管理</span><span class="close" @click="closePopup">&#xe64e;</span>
      </div>
      <div class="main">
        <div class="form-item">
          <p>
            <span class="lable">用户名：</span>
            <input type="text" class="input-item">
          </p>
          <p>
            <span class="lable">姓名：</span>
            <input type="text" class="input-item">
          </p>
          <p>
            <span class="lable">性别：</span>
            <input type="text" class="input-item">
          </p>
          <p>
            <span class="lable">手机号：</span>
            <input type="text" class="input-item">
          </p>
          <p>
            <span class="lable">密码：</span>
            <input type="password" class="input-item">
          </p>
          <p>
            <span class="lable">确认密码：</span>
            <input type="password" class="input-item">
          </p>
          <p>
            <span class="lable">备注：</span>
            <input type="text" class="input-item w5">
          </p>
          <p>
            <span class="lable">地址：</span>
            <input type="text" class="input-item w5">
          </p>
          <p>
            <span class="lable">负责菜：</span>
            <span class="check-box">
              <input type="checkbox" class="input-item w1" checked><span class="check-item">西红柿炒蛋</span>
              <input type="checkbox" class="input-item w1"><span class="check-item">西红柿炒蛋</span>
              <input type="checkbox" class="input-item w1"><span class="check-item">西红柿炒蛋</span>
              <input type="checkbox" class="input-item w1"><span class="check-item">西红柿炒蛋</span>
              <input type="checkbox" class="input-item w1"><span class="check-item">西红柿炒蛋</span>
              <input type="checkbox" class="input-item w1" checked><span class="check-item">西红柿炒蛋</span>
              <input type="checkbox" class="input-item w1"><span class="check-item">西红柿炒蛋</span>
              <input type="checkbox" class="input-item w1"><span class="check-item">西红柿炒蛋</span>
              <input type="checkbox" class="input-item w1"><span class="check-item">红烧排骨</span>
              <input type="checkbox" class="input-item w1" checked><span class="check-item">红烧排骨</span>
              <input type="checkbox" class="input-item w1" checked><span class="check-item">红烧排骨</span>
              <input type="checkbox" class="input-item w1"><span class="check-item">红烧排骨</span>
              <input type="checkbox" class="input-item w1" checked><span class="check-item">红烧排骨</span>
              <input type="checkbox" class="input-item w1"><span class="check-item">鱼香肉丝</span>
              <input type="checkbox" class="input-item w1"><span class="check-item">鱼香肉丝</span>
              <input type="checkbox" class="input-item w1"><span class="check-item">鱼香肉丝</span>
            </span>
          </p>
        </div>
      </div>
      <div class="footer">
        <p><span class="cancel" @click="closePopup">取消</span><span class="save">保存</span></p>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'CAdd',
  methods: {
    closePopup () {
      this.$emit('closeAdd', false)
    }
  }
}
</script>

<style lang="stylus" scoped>
@import '~styles/viewpadding.styl'
  .mask
    mask()
    h-v-center()
    top 1.04rem
    .popup
      width 11.2rem
      height 9.7rem
      background #fff
      .header
        popup-header-footer()
        .title
          float left
          text-indent .7rem
        .close
          font-family 'iconfont'
          padding-right .42rem
      .main
        height 7.7rem
        margin-top 0.5rem
        .form-item
          display flex
          justify-content center
          flex-wrap wrap
          box-sizing border-box
        p
          height .6rem
          line-height .6rem
          margin-bottom .32rem
          margin-left .32rem
          .lable
            width 1.4rem
            display inline-block
          .input-item
            border 1px solid #a9a9a9
            width 3.48rem
            line-height .55rem
          .w5
            width 8.78rem
          .check-box
            display inline-table
            width 8.78rem
            .w1
                width 0.4rem
            .check-item
                margin-right 0.3rem
                width 1.4rem
                display inline-block
      .footer
        popup-header-footer()
        padding-right .2rem
        .cancel
        .save
          list-button()
          margin-right .58rem
</style>



