<template>
  <div class="view">
    <div class="view-container">
      <c-header @openAdd="handleOpenAdd"/>
      <c-table @openAdd="handleOpenAdd"/>
      <c-pagination/>
      <add-person v-show="add_seen" @closeAdd="handleCloseAdd" ></add-person>
    </div>
  </div>
</template>

<script>
import CHeader from './components/Header'
import CTable from './components/Table'
import CPagination from './components/Pagination'
import AddPerson from './components/AddPerson'
export default {
  name: 'PersonManage',
  data () {
    return {
      add_seen: false
    }
  },
  components: {
    CHeader,
    CTable,
    CPagination,
    AddPerson
  },
  methods: {
    handleOpenAdd: function () {
      this.$data.add_seen = true
    },
    handleCloseAdd: function () {
      this.$data.add_seen = false
    }
  }
}
</script>

<style lang="stylus" scoped>
@import '~styles/viewpadding.styl'
  .view
    min-width 5.6rem
    pad1()
    .view-container
      pad2()
      min-height 50vw
      position relative
      box-sizing border-box
      // min-width 11.2rem
</style>



