<template>
  <div class="header">
    <div class="title">统计</div>
    <div class="header-content">
       <div class="content-title"><span>历史上菜数</span></div>
      <div class="header-right">
        <div class="search">
          <select class="status">
            <option value="1">按年统计</option>
            <option value="0">按月统计</option>
          </select>
          <select class="status">
            <option value="1">2018</option>
            <option value="0">2017</option>
          </select>
          <div class="search-btn">&#xe632;</div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'CHeader'
}
</script>

<style lang="stylus" scoped>
@import '~styles/viewpadding.styl'
  .header
    space-between()
    box-sizing border-box
    .title
      mb028()
      min-width 2rem
      font-size .36rem
      height .56rem
      line-height .56rem
      width 100%
    .header-content
      space-between()
      mb028()
      box-sizing border-box
      font-size .36rem
      line-height .56rem
      width 100%
      .content-title
        mb028()
      .header-right
        space-between()
        box-sizing border-box
        min-width 6rem
        .search
          space-between()
          min-width 4rem
          box-sizing border-box
          .status
            mb028()
            width 3rem
            border 1px solid #ccc
            margin-right .28rem
          .search-input
            mb028()
            background #eee
            height .56rem
            line-height .56rem
            min-width 4.66rem
            padding 0 .1rem
            border-radius .1rem
          .search-btn
            mb028()
            min-width .7rem
            height .56rem
            line-height .56rem
            font-family 'iconfont'
            font-size .40rem
            color #aaf
            cursor pointer
            margin-left .14rem
</style>



