<template>
  <div class="view">
    <div class="view-container">
      <c-header></c-header>
      <c-charts></c-charts>
    </div>
  </div>
</template>

<script>
import CHeader from './components/Header'
import CCharts from './components/Charts'
export default {
  name: 'Statistics',
  components: {
    CHeader,
    CCharts
  }
}
</script>

<style lang="stylus" scoped>
@import '~styles/viewpadding.styl'
  .view
    min-width 5.6rem
    pad1()
    .view-container
      pad2()
      min-height 50vw
      position relative
      box-sizing border-box
      // min-width 11.2rem
</style>



